import * as Icon from 'react-bootstrap-icons';
import { useState } from 'react';
import { Link } from 'react-router-dom';
const NavBar = ({postsList}) =>{
    //States
    const [searchText, setSearchText] = useState('')
    const [displayShow, setDisplayShow] = useState(false)
    const [foundPosts, setFoundPosts] = useState([])
    //Handelers
    const handleSearchBarChange = e =>{
        setSearchText(e.target.value)
        if (e.target.value !== '') {
            setDisplayShow(true)
            let foundsList = [];
            postsList.forEach(post => {
            if(post.title.toLowerCase().includes(e.target.value.toLowerCase())){
            foundsList.push(post)
            }else{
                    return null;
            }
            });
            setFoundPosts(foundsList)
        }
        else{
            setDisplayShow(false)
            setFoundPosts([])
        }
    }
    return(
        <nav className='navBar'>
  
            <Link to='/' className='navText'>
                <Icon.CupFill/>
                CoffeePosts
            </Link>   

            <div className='navSocailFunctions'>
                <ul>
                    <li>
                        <Link to='/Chat'>
                            <div>
                                <Icon.Chat/>
                                Chat
                            </div>
                        </Link>
                    </li>
                    <li>
                        <Link to='/Forums'>
                            <div>
                                <Icon.XDiamond/>
                                Forums
                            </div>
                        </Link>
                    </li>
                </ul>
            </div>

            <div className='navSearch'>
                <Icon.Search/>
                <input value={searchText} onChange={handleSearchBarChange} type='text'></input>
                <section className='searchDisplay' style={displayShow ? {} : {display:'none'}}>
                    <h6>Posts</h6>
                    {foundPosts.length > 0
                    ?
                    <div> 
                        {foundPosts.map(post=>(
                            <button className='navBarFounded'> 
                                <Link 
                                onClick={() => setDisplayShow(false)} 
                                to={`/post/${post._id}`} 
                                style={{color:'grey'}}> 
                                <span style={{color:'white'}}>
                                {post.title}
                                </span>
                                - forum - user 
                                </Link>
                            </button>
                        ))} 
                    </div>
                    : 
                    <h6 style={{color:'grey'}} className='mt-2'>Nothing Found!</h6>}
                </section>
            </div>       
   

        </nav>
    );
}

export default NavBar; 