import './App.css';
import { useState, useEffect } from 'react';
import axios from 'axios'
import Home from './Home'
//Routes =>
import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import NavBar from './components/NavBar';
import SelectedPost from './SelectedPost'
//ROutes <=
function App() {
  const [postsList, setPostsList] = useState([])
  useEffect( () =>{
    axios.get('/api/posts')
      .then(res =>{
        setPostsList(res.data.posts)
      })
    
  }, [])

  const addPost = (post) =>{
    axios.post('/api/posts', post)
      .then(res => {setPostsList(prevState => [res.data, ...prevState])});
  };

  const deletePost = (id) =>{
    let newPostList = []
    axios.delete(`/api/posts/${id}`)
      .then(res =>{
        postsList.forEach(post => post._id === id ? null : newPostList.push(post));
        setPostsList(newPostList);
      });
  }

  const updatePost = (id, newPost) =>{
    axios.put(`/api/posts/${id}`, newPost)
      .then(res => setPostsList(prevState => prevState.map(post => post._id !== id ? post : res.data)))
  }
  
  return (

    <Router>
    <div className='main'>
      <NavBar
      postsList={postsList}
      />
      <hr className='my-5'></hr>
      <Switch>
      <Route path="/Post/:postId&:editingParam">
          <SelectedPost 
            addPost={addPost}
            deletePost={deletePost} 
            updatePost={updatePost}
            setPostsList={setPostsList}
          />
      </Route>
      <Route path="/Post/:postId">
          <SelectedPost 
            addPost={addPost}
            setPostsList={setPostsList}
            deletePost={deletePost} 
            updatePost={updatePost}
          />
      </Route>
      <Route path="/Forums">
      </Route>
      <Route path="/Chat">
      </Route>
      <Route path="/">
          <Home 
          postsList={postsList}
          addPost={addPost}
          deletePost={deletePost} 
          updatePost={updatePost}
          />
      </Route>
      </Switch>
    </div>
  </Router>
  );
}
